:root {
  --gray: 127, 127, 127;
  --light: 254, 254, 254;
  --light-dark: 238, 238, 238;
  --dark: 33, 33, 33;
  --dark-dark: 21, 21, 21;
  --blue: 0, 122, 255;
  --blue-dark: 10, 122, 255;
  --green: 52, 199, 89;
  --green-dark: 48, 209, 88;
  --indigo: 88, 86, 214;
  --indigo-dark: 94, 92, 230;
  --orange: 255, 149, 0;
  --orange-dark: 255, 159, 10;
  --pink: 255, 45, 85;
  --pink-dark: 255, 55, 95;
  --purple: 175, 82, 222;
  --purple-dark: 191, 90, 242;
  --red: 255, 59, 48;
  --red-dark: 255, 69, 58;
  --teal: 90, 200, 250;
  --teal-dark: 100, 210, 255;
  --yellow: 255, 204, 0;
  --yellow-dark: 255, 214, 10;
  --alpha-soft: 0.24;
  --alpha-medium: 0.48;
  --alpha-strong: 0.8;
  -webkit-tap-highlight-color: transparent;
  & * {
    box-sizing: border-box;
  }
}

html, body, #root {
  margin: 0;
  min-height: 100vh;
  width: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.2;
  overflow-x: hidden;
}

@media (prefers-color-scheme: dark) {
  body {
    color: rgba(var(--light), 1);
    background-color: rgba(var(--dark-dark), 1);
  }
}

@media (prefers-color-scheme: light) {
  body {
    color: rgba(var(--dark-dark), 1);
    background-color: rgba(var(--light-dark), 1);
  }
}

.page {
  padding-bottom: calc(57px + env(safe-area-inset-bottom));
}

.fab {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  right: 16px;
  bottom: calc(57px + 16px + env(safe-area-inset-bottom));
  background-color: rgba(var(--blue), 1);
  color: rgba(var(--light), 1);
  width: 48px;
  height: 48px;
  text-decoration: none;
  border-radius: 50px;
  z-index: 999;
  box-shadow: 0 8px 24px rgba(var(--dark-dark), var(--alpha-soft));
  outline: none;
  transition: all 0.24s ease;
  & > svg {
    fill: currentColor;
  }
  &:hover,
  &:focus {
    background-color: rgba(var(--blue-dark), 1);
    transform: rotate(90deg);
  }
  &:active {
    background-color: rgba(var(--blue-dark), var(--alpha-strong));
  }
}
